
























































import Vue from 'vue';
import { getTransactions } from '@/api/eth';
import i18n from '@/locale';

export default Vue.extend({
  props: {
    address: {
      type: String,
    },
    target: {
      type: String,
      default: 'eoa',
    },
  },
  data: () => ({
    isLoaded: false,
    results: [] as any,
    headers: [
      {
        text: i18n.t('TransactionHash'),
        value: 'hash',
        sortable: false,
      },
      {
        text: i18n.t('TransactionFrom'),
        value: 'from',
        sortable: false,
      },
      {
        text: i18n.t('TransactionTo'),
        value: 'to',
        sortable: false,
      },
      {
        text: i18n.t('TransactionSignature'),
        value: 'signature',
        sortable: true,
      },
    ],
    itemsPerPage: 10,
    page: 1,
    persistAddress: '',
  }),
  methods: {
    async fetchList() {
      switch (this.target) {
        case 'eoa':
          this.results = await getTransactions({
            where: {
              from: this.address,
            },
            sort: {
              transactionIndex: -1,
              blockNumber: -1,
            },
            skip: (this.page - 1) * this.itemsPerPage,
            limit: this.itemsPerPage,
          });
          this.isLoaded = true;
          break;
        case 'erc20':
          this.results = await getTransactions({
            where: {
              to: this.address,
            },
            sort: {
              transactionIndex: -1,
              blockNumber: -1,
            },
            skip: (this.page - 1) * this.itemsPerPage,
            limit: this.itemsPerPage,
          });
          this.isLoaded = true;
          break;
        default:
          break;
      }
    },
    async redirect(item: any) {
      await this.$router.push(`/explorer/tx/${item.hash}`);
    },
    setItemPerPage(count: number) {
      this.itemsPerPage = count;
      this.fetchList();
    },
    setPage(index: number) {
      this.page = index;
      this.fetchList();
    },
    nextPage() {
      if (this.results.length < 10) return;
      this.page += 1;
      this.fetchList();
    },
    prevPage() {
      if (this.page === 1) return;
      this.page -= 1;
      this.fetchList();
    },
  },
  mounted() {
    this.fetchList();
    this.persistAddress = this.address;
  },
});
