
















import Vue from 'vue';
import i18n from '@/locale';

import Constants from '@/constants';

export default Vue.extend({
  name: 'ContractResult',
  data: () => ({
    headers: [
      {
        text: i18n.t('ContractName'),
        align: 'start',
        value: 'name',
      },
      {
        text: i18n.t('ContractAddress'),
        value: 'address',
        sortable: false,
      },
    ],
    contracts: [] as any,
  }),
  methods: {
    redirect(item: any) {
      this.$router.push(`/explorer/contract/${item.address}?name=${encodeURIComponent(item.name)}`);
    }
  },
  created() {
    Object.keys(Constants.explorer.contracts).forEach((token: string) => {
      this.contracts.push({
        name: token,
        address: Constants.explorer.contracts[token],
      })
    })
  }
});
