
















































import Vue from 'vue';
import i18n from '@/locale';

import { mapState } from 'vuex';

interface Token {
  name: string;
  address: string;
  totalSupply: string;
}

export default Vue.extend({
  name: 'Token',
  data: () => ({
    isLoaded: false,
    currency: '$',
    snackbar: false,
    snackbarContent: '',
    headers: [
      {
        text: i18n.t('TokenName'),
        align: 'start',
        value: 'name',
      },
      {
        text: i18n.t('TokenContractAddress'),
        value: 'address',
        sortable: false,
      },
      {
        text: i18n.t('TokenTotalSupply'),
        value: 'totalSupply',
      },
      // {
      //   text: i18n.t('TokenMarketCap'),
      //   value: 'marketCap'
      // },
      // {
      //   text: i18n.t('TokenHolders'),
      //   value: 'holder',
      // },
      // {
      //   text: i18n.t('TokenTransactions'),
      //   value: 'transactions',
      // }
    ],
  }),
  computed: {
    ...mapState('explorer', [
      'tokens'
    ])
  },
  methods: {
    async fetchList() {
      this.isLoaded = false;
      await this.$store.dispatch('explorer/fetchTokenList');
      this.isLoaded = true;
    },
    copy(content: string) {
      navigator.clipboard.writeText(this.snackbarContent);
      this.snackbarContent = content;
      this.snackbar = true;
    },
    redirect(item: any) {
      this.$router.push(`/explorer/token/${item.address}?name=${item.name}`)
    }
  },
  mounted() {
    this.fetchList();
  },
});
