























































































import Vue from 'vue';

// import Overview from './Overview.vue';
import Contract from './Contract.vue';
import Token from './Token.vue';
import Transaction from './Transaction.vue';
import Result from './Result.vue';

export default Vue.extend({
  name: 'Explorer',
  components: {
    // Overview,
    Contract,
    Token,
    Transaction,
    Result,
  },
  data: () => ({
    items: [
      {
        name: 'ExplorerTxHash',
        disabled: false,
      },
      {
        name: 'ExplorerAddr',
        target: 'eoa',
        disabled: false,
      },
      {
        name: 'Explorer20Token',
        target: 'erc20',
        disabled: false,
      },
      {
        name: 'Explorer721Token',
        disabled: true,
      },
      {
        name: 'ExplorerDex',
        disabled: true,
      },
    ],
    itemSelected: 0,
    tab: 0,
    input: '',
    showSearchResult: false,
  }),
  methods: {
    async search() {
      console.log(this.itemSelected);
      switch (this.itemSelected) {
        case 0:
          await this.$router.push(`/explorer/tx/${this.input}`);
          break;
        case 1:
          this.showSearchResult = false;
          this.showSearchResult = true;
          this.tab = 2;
          break;
        case 2:
          this.showSearchResult = false;
          this.showSearchResult = true;
          this.tab = 2;
          break;
      }
    }
  }
});
